.btn {
  border: 0;
  margin: 0;
  cursor: pointer;
  transition: all 0.3s;
  border-radius: 0.2em;
  height: auto;
  border: 1px solid #aaa;
  box-shadow: 0 0 rgba(17, 17, 17, 0) inset;
  padding: 0.5em 1em;
  font-size: 1rem;
}

.btn span {
  height: 100%;
  font-size: 1.1rem;
  line-height: 1em;
  vertical-align: middle;
}

.btn-icon {
  height: 1em;
  width: 1em;
  vertical-align: middle;
  color: var(--color-on-primary);
}
.btn-icon path {
  fill: var(--color-on-primary);
}

.btn-primary {
  color: var(--color-on-primary);
  background-color: var(--primary-color);
  border: 1px solid var(--border-color);
}
