.editor {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: start;
  justify-content: center;

  width: 100%;
  height: 100%;
  margin: auto;
  padding: 0;
  box-sizing: border-box;
}
.editor > div {
  width: 50%;
  margin: 0;
  padding: var(--space-block);
  box-sizing: border-box;
}
.editor ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
