.paragraph {
  margin: 0;
  margin-bottom: var(--space-block);
  padding: 0 !important;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-items: stretch;
  align-items: stretch;
  width: 100%;

  box-sizing: border-box;
}

.textarea {
  background-color: white;
  display: flex;
  padding: 0;
  position: relative;
  flex-grow: 1;
  box-sizing: border-box;
}
.textarea .label-text {
  display: none;
}
.textarea textarea {
  resize: vertical;
  width: 100%;
  max-width: 100%;
  padding: var(--space-small);
  background-color: transparent;
  min-height: 1.325em;
  height: 125px; /* 19px * 1.3125 * 5 */
  font-size: 19px;
  line-height: 1.3125;
  color: var(--color-on-primary);

  border-radius: 0;
  border-width: 0;
}
.textarea textarea:focus {
  border: none;
}

.count {
  position: absolute;
  bottom: 0;
  right: 0;
  width: calc(100% - 1.5rem);
  box-sizing: border-box;
  padding-bottom: var(--space-small);

  font-weight: bold;
  font-size: 1rem;
  text-align: right;
  margin-right: 1.5rem;

  touch-action: none;
}

.edit-form {
  width: 0;
  flex: 0 0 auto;
  overflow: hidden;
  transition: width 0.1s ease-out;
  border-style: solid;
  border-color: var(--border-color);
  border-width: 0;
  box-sizing: border-box;
}
.edit-form svg {
  width: 1.5rem;
  height: 1.5rem;
}

.paragraph[data-is-editing="false"] .edit-form {
  width: 0;
}
.paragraph[data-is-editing="true"] .count {
  display: none;
}

.delete {
  width: 4rem;
  display: flex;
  align-items: center;
}

.delete > button {
  height: 100%;
  width: 100%;
  max-width: 100%;
  background-color: var(--color-danger);
  border: 1px solid var(--color-danger);
  box-sizing: border-box;
  border-radius: 0 0.2rem 0.2rem 0;
}
.delete path {
  fill: white;
}

.move {
  width: 4rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.move-button {
  width: 100%;
  min-height: 2rem;
  height: 50%;
  border-color: var(--border-color);
  border-width: 0 1px 0 0;
  border-radius: 0;
}
.move .move-button:first-child {
  border-top-left-radius: 0.2rem;
  border-bottom-width: 1px;
}

.move .move-button:nth-child(2) {
  border-bottom-left-radius: 0.2rem;
}

.move-button:disabled {
  opacity: 0.5;
}
